/* 검색 폼에 스타일 적용 */
.TodoList .searchbar{
    margin-bottom: 20px;
    width: 100%;
    border: none;
    border-bottom: 1px solid rgb(220,220,220);
    box-sizing: border-box;
    padding-top: 15px;
    padding-bottom: 15px;
}

/* 검색 폼을 클릭했을 때의 스타일 적용 */
.TodoList .searchbar:focus{
    outline: none;
    border-bottom: 1px solid #1f93ff;
}

.TodoList .list_wrapper{
    display: flex;
    flex-direction: column;
    gap: 20px;
}